.pagination-container {
  width: 100%;
  display: flex;
  font-size: 2rem;
  margin-top: 4rem;
  gap: 0.8em;
  justify-content: center;
  color: $primary;
  font-weight: 300;
  flex-wrap: wrap;

  span {
    cursor: pointer;
  }

  .arrow {
    cursor: pointer;
    height: 2em;
    width: 2em;
    background-color: rgba(128, 128, 128, 0);
    border-radius: 50%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
      background-color: rgba(128, 128, 128, 0.1);
    }

    &::after,
    &::before {
      content: "";
      position: absolute;
      width: 0.6em;
      height: 0.2em;
      background-color: $primary;
      border-radius: 2em;
    }

    &--right::after {
      bottom: 54%;
      transform: rotate(45deg);
    }

    &--right::before {
      top: 54%;
      transform: rotate(-45deg);
    }

    &--left::after {
      bottom: 54%;
      transform: rotate(-45deg);
    }

    &--left::before {
      top: 54%;
      transform: rotate(45deg);
    }
  }

  .number {
    $opacity: 0;
    border-radius: 100%;
    height: 2em;
    width: 2em;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(128, 128, 128, $opacity);
    cursor: pointer;

    &:hover {
      background-color: rgba(128, 128, 128, 0.1);
    }

    span {
      font-size: 0.8em;
      font-weight: 700;
    }

    &.actual {
      background-color: rgba(128, 128, 128, 0.4);

      &:hover {
        background-color: rgba(128, 128, 128, 0.8);
      }
    }
  }
}
